import React, { useState } from "react";
import { Button } from "./ui/button";
import { Menu, X, Car } from "lucide-react";

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: "smooth" });
    }
    setIsMenuOpen(false);
  };

  return (
    <header className="fixed top-0 w-full bg-white/95 backdrop-blur-sm z-50 border-b border-slate-200">
      <div className="container mx-auto px-4 py-4">
        <div className="flex items-center justify-between">
          {/* Logo */}
          <div className="flex items-center space-x-2">
            <div className="bg-slate-900 p-2 rounded-lg">
              <Car className="h-6 w-6 text-white" />
            </div>
            <span className="text-xl font-bold text-slate-900">
              Elite Auto Detailing
            </span>
          </div>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center space-x-8">
            <button
              onClick={() => scrollToSection("home")}
              className="nav-link text-slate-600 hover:text-slate-900"
            >
              Home
            </button>
            <button
              onClick={() => scrollToSection("services")}
              className="nav-link text-slate-600 hover:text-slate-900"
            >
              Services
            </button>
            <button
              onClick={() => scrollToSection("about")}
              className="nav-link text-slate-600 hover:text-slate-900"
            >
              About
            </button>
            <button
              onClick={() => scrollToSection("testimonials")}
              className="nav-link text-slate-600 hover:text-slate-900"
            >
              Reviews
            </button>
            <Button
              onClick={() => scrollToSection("contact")}
              className="bg-slate-900 hover:bg-slate-800 text-white"
            >
              Book Now
            </Button>
          </nav>

          {/* Mobile Menu Button */}
          <button
            className="md:hidden"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
          >
            {isMenuOpen ? (
              <X className="h-6 w-6 text-slate-900" />
            ) : (
              <Menu className="h-6 w-6 text-slate-900" />
            )}
          </button>
        </div>

        {/* Mobile Navigation */}
        {isMenuOpen && (
          <nav className="md:hidden mt-4 py-4 border-t border-slate-200">
            <div className="flex flex-col space-y-4">
              <button
                onClick={() => scrollToSection("home")}
                className="nav-link text-slate-600 hover:text-slate-900 text-left"
              >
                Home
              </button>
              <button
                onClick={() => scrollToSection("services")}
                className="nav-link text-slate-600 hover:text-slate-900 text-left"
              >
                Services
              </button>
              <button
                onClick={() => scrollToSection("about")}
                className="nav-link text-slate-600 hover:text-slate-900 text-left"
              >
                About
              </button>
              <button
                onClick={() => scrollToSection("testimonials")}
                className="nav-link text-slate-600 hover:text-slate-900 text-left"
              >
                Reviews
              </button>
              <Button
                onClick={() => scrollToSection("contact")}
                className="bg-slate-900 hover:bg-slate-800 text-white w-full"
              >
                Book Now
              </Button>
            </div>
          </nav>
        )}
      </div>
    </header>
  );
};

export default Header;